import { useState, useEffect } from 'react';
import { getAllCategories } from '../api';
import { Categorylist } from '../Components/CategoryList';
import { Search } from '../Components/Search';
import { useLocation, useHistory } from 'react-router-dom';

export const Home = () => {
  const [ catalog, setCatalog ] = useState([]);
  const [ filteredCatalog, setFilteredCatalog ] = useState([]);

  const { pathname, search } = useLocation();
  const {push} = useHistory()

  const handleSearch = (str) => {
    setFilteredCatalog(
      catalog.filter((item) => item.strCategory.toLowerCase().includes(str.toLowerCase())),
    );
    //ÐÐ¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð² Ð¿Ð¾Ð¸ÑÐºÐ¾Ð²ÑÑ ÑÑÑÐ¾ÐºÑ Ð½Ð°Ð·Ð²Ð°Ð½Ð¸Ñ ÑÐ¾Ð³Ð¾ ÑÑÐ¾ Ð¸ÑÐµÐ¼
    push({
      pathname,
      search:`?search=${str}`
    })
  };

  useEffect(() => {
    getAllCategories().then((data) => {
      setCatalog(data.categories);
      setFilteredCatalog(search ? data.categories.filter(item=>item.strCategory.toLowerCase().includes(search.split('=')[1].toLowerCase())): data.categories);
    });
  }, [search]);
  return (
    <div>
      <Search cb={handleSearch} />
      <Categorylist catalog={filteredCatalog} />
    </div>
  );
};
